.Result {
    margin: 0;
    position: absolute;
    width: 100%;
    height: 100%;

    display: flex;
    align-items: center;
    flex-direction: column;
    background: var(--background_content);
    min-height: 100vh;
    padding: 0px 10px 0px 10px;
    box-sizing: border-box;
}

.Result__title {
    color: white;
}

.Result__year {
    font-weight: 500;
    text-align: center;
    font-size: 100px;
    line-height: 76px;
    font-family: 'Lato';
    color: var(--main-white-color);
    white-space: nowrap;
}

.Result__year-prefix {
}

.Result__year-postfix_good {
    color: #79C52D;
}

.Result__year-postfix_normal {
    color: #E0C225;
}

.Result__year-postfix_bad {
    color: var(--main-second-red-color);
}

.Result__historical-event {
    color: var(--text_secondary);
    font-size: 20px;
    text-align: center;
    padding-top: 12px;
}

.Result__buttons-container {
    /* display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 24px -7px 24px -7px; */
    margin-top: 10px;
    background-color: var(--main-second-bg-color);
    border-radius: 15px;
    max-width: 370px;
    width: 100%;

    padding: 10px;
    box-sizing: border-box;

    /* display: grid;
    grid-template-columns: 1fr; */
}

.Result__button {
    background: var(--background_content);
    margin-top: 10px;
    border-radius: 10px;
}
.Result__button:first-child{
    margin-top: 0;
}

